import { Injectable } from '@angular/core';
import { HttpDispatcherService } from '@app/@core/http/http-dispatcher.service';
import { Observable } from 'rxjs';
const routes = {
  api: {
    getAll: () => `/api/Mobile/notifications`,
  },
};
@Injectable({
  providedIn: 'root',
})
export class NotificationService {
  constructor(private httpDispatcher: HttpDispatcherService) {}
  getAll(): Observable<any> {
    return this.httpDispatcher.get(routes.api.getAll());
  }
}
