import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AlertsComponent } from './alerts/alerts.component';
import { AttendenceComponent } from './attendence/attendence.component';
import { CommunicationComponent } from './communication/communication.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ExaminationComponent } from './examination/examination.component';
import { NotificationComponent } from './notification/notification.component';
import { OnlineLearningComponent } from './online-learning/online-learning.component';

import { SupportComponent } from './support/support.component';
import { TimetableComponent } from './timetable/timetable.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
  },
  {
    path: 'support',
    component: SupportComponent,
    data: { title: 'Support' },
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    data: { title: 'Dashboard' },
  },
  {
    path: 'online-learning',
    component: OnlineLearningComponent,
    data: { title: 'Online Learning' },
  },
  {
    path: 'alerts',
    component: AlertsComponent,
    data: { title: 'Alerts' },
  },
  {
    path: 'communication',
    component: CommunicationComponent,
    data: { title: 'Communication' },
  },
  {
    path: 'notification',
    component: NotificationComponent,
    data: { title: 'Notification' },
  },
  {
    path: 'examination',
    component: ExaminationComponent,
    data: { title: 'Examination' },
  },
  {
    path: 'timetable',
    component: TimetableComponent,
    data: { title: 'Timetable' },
  },
  {
    path: 'attendence',
    component: AttendenceComponent,
    data: { title: 'Attendence' },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule {}
