import { Injectable } from '@angular/core';
import { HttpDispatcherService } from '@app/@core/http/http-dispatcher.service';
import { Observable } from 'rxjs';
const routes = {
  api: {
    getAll: () => `/api/mobile/attendancenew`,
  },
};
@Injectable({
  providedIn: 'root',
})
export class AttendenceService {
  constructor(private httpDispatcher: HttpDispatcherService) {}
  getAll(year: string): Observable<any> {
    return this.httpDispatcher.get(routes.api.getAll() + `?year=${year}&month=0`);
  }
}
