import { Injectable } from '@angular/core';
import { HttpDispatcherService } from '@app/@core/http/http-dispatcher.service';
import { Observable } from 'rxjs';
const routes = {
  alerts: {
    // getAll: (filters?: Map<string, string>) => (filters ? `/users?${filters.toSanitizedURLFilters()}` : `/users`),
    getAllAlerts: () => `/api/Mobile/alerts`,
  },
};

@Injectable({
  providedIn: 'root',
})
export class AlertsService {
  constructor(private httpDispatcher: HttpDispatcherService) {}

  // Update profile
  getAllAlerts(): Observable<any> {
    return this.httpDispatcher.get(routes.alerts.getAllAlerts());
  }
}
