import { formatDate } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { User } from '@app/@core/models/users.model';
import { UserService } from '@app/@core/services/user.service';
import { CommunicationService } from './communication.service';
interface Communication {
  id: string;
  type: string;
  date: Date;
  sender: string;
  Text: string;
  actionTheme: string;
  actionText: string;
  Download: string;
  DownloadPath: string;
  AcknowledgeRequired: string;
  Acknowledged: string;
  Duration: string;
  UnreadCount: string;
  ModuleName: string;
  SendTo: string;
  SenderNoty: string;
  URLs: string;
}
@Component({
  selector: 'app-communication',
  templateUrl: './communication.component.html',
  styleUrls: ['./communication.component.scss'],
})
export class CommunicationComponent implements OnInit {
  constructor(private userService: UserService, private service: CommunicationService) {}
  whoAmI: User;
  currentDate: Date;
  txtDate: string;
  ngOnInit(): void {
    this.userService.whoAmI().subscribe((response) => {
      this.whoAmI = response[0];
      this.currentDate = new Date();
      this.getCommunication();
    });
  }
  getCommunication() {
    this.txtDate = formatDate(this.currentDate, 'yyyy-MM-dd', 'en-US');
    this.service
      .getAllCommunication(this.whoAmI.UserId, formatDate(this.currentDate, 'yyyy-MM-dd', 'en-US'))
      .subscribe((response) => {
        console.log(response);
      });
  }
  setNextDay() {
    this.currentDate.setDate(this.currentDate.getDate() + 1);
    this.getCommunication();
  }
  setPrevioutDay() {
    this.currentDate.setDate(this.currentDate.getDate() - 1);
    this.getCommunication();
  }
}
