import { Injectable } from '@angular/core';
import { HttpDispatcherService } from '@app/@core/http/http-dispatcher.service';
import { Observable } from 'rxjs';

const routes = {
  alerts: {
    // getAll: (filters?: Map<string, string>) => (filters ? `/users?${filters.toSanitizedURLFilters()}` : `/users`),
    getAllCommunication: () => `/api/Mobile/dailydiary`,
  },
};

@Injectable({
  providedIn: 'root',
})
export class CommunicationService {
  constructor(private httpDispatcher: HttpDispatcherService) {}
  getAllCommunication(rollNo: string, date: string): Observable<any> {
    return this.httpDispatcher.get(routes.alerts.getAllCommunication() + `?rollno=${rollNo}&date=${date}&action=`);
  }
}
